import React from 'react';
import styles from'./style/index.less';
import { connect } from 'pipenet-core/lib/react';
import CountUp from 'react-countup';

@connect(state => ({
  comprehensiveN: state.comprehensiveN.zhComprehensive,
}))
export default class extends React.Component {
  state = {
    timer: null// 
  }
  onClose() {
    console.log('onClose');
  }
  query = () => {
    const payload = {};
    this.props.dispatch({
      type: 'comprehensiveN/information',
      payload,
    });
  };
  componentWillMount() {
    this.query();
  }
  componentDidMount() {
  
  }
  render() {
    const { comprehensiveN } = this.props;
    console.log(this.props);
    if(comprehensiveN === undefined){
      return null;
      }else {

        return (
          <div className={styles.zonghe}>
            <div
              className={styles.comprehensive}
              style={{
                borderImage: "url('/images/imgs/.png') 0 0 0 0 fill round",
                borderImageRepeat: 'stretch',
              }}
            >
              <a href={':javascript'}></a>
            </div>
            <div
              className={styles.realtime}
              style={{
                borderImage: "url('/images/imgs/.png') 5 1 1 5 fill round",
                borderImageRepeat: 'stretch',
              }}
            >
              <div
                className={styles.semicircleframe}
                style={{ background: "url('/images/imgs/-.png') no-repeat" }}
              >
                <p className={styles.one}>
                  <span  className={styles.web_font}><CountUp start={0} end={1066} /></span>
                  
                </p>
                <p className={styles.fuwu}></p>
              </div>
              <div
                className={styles.semicircleframe}
                style={{ background: "url('/images/imgs/-.png') no-repeat" }}
              >
                <p className={styles.one}>
                  <span><CountUp start={0} end={13573} /></span>
                </p>
                <p className={styles.fuwu}></p>
              </div>
              <div
                className={styles.semicircleframe}
                style={{ background: "url('/images/imgs/-.png') no-repeat" }}
              >
                <p className={styles.one}>
                  <span><CountUp start={0} end={396} /></span>
                </p>
                <p className={styles.fuwu}></p>
              </div>
              <div
                className={styles.semicircleframe}
                style={{ background: "url('/images/imgs/-.png') no-repeat" }}
              >
                <p className={styles.one}>
                  <span> <CountUp start={0} end={comprehensiveN.total.total.toString().split('.')[0]} /> </span>
                  
                </p>
                <p className={styles.fuwu}></p>
              </div>
              <div
                className={styles.semicircleframe}
                style={{ background: "url('/images/imgs/-.png') no-repeat" }}
              >
                <p className={styles.one}>
                  <span> <CountUp start={0} end={comprehensiveN.punSum.punsum} /></span>
                </p>
                <p className={styles.fuwu}></p>
              </div>
              <div
                className={styles.semicircleframe}
                style={{ background: "url('/images/imgs/-.png') no-repeat" }}
              >
                <p className={styles.one}>
                  <span> <CountUp start={0} end={comprehensiveN.terminalSum.terminalsum} /> </span>
                </p>
                <p className={styles.fuwu}></p>
              </div>
            </div>
          </div>
        );
      }
  }
}
