
import { information } from '../services/comprehensive.js';
export default {
  namespace: 'comprehensiveN',
  state: {
    technologyData: 0,
    opend: '',
  },

  effects: {
    *information({ payload }, { call, put }) {
      let technologyData;
      const response = yield call(information,payload);
      console.log(363636363636);
      console.log(response);
      technologyData = response.result;
      yield put({
        type: 'comprehensive2',   
        payload: technologyData,
      });
    },
  },
  reducers: {
    comprehensive2(state, { payload }) {
      return { ...state,zhComprehensive: payload };
    },
  },
  setOpened(state) {
    return {
      ...state,
      opend: true,
    }
  }
};
